












































































































































































































































































































































































































#tablet{
  position: relative;
  .ml20 {
    margin-left: 20px;
  }
  .mr20 {
    margin-right: 20px;
  }
  .mt15 {
    margin-top: 15px;
  }
  .pull-right {
    float: right;
  }
  .btn-tabBtn {
    background: rgb(16,186,204);
    border-color: rgb(16,186,204);
    margin-right: 10px;
    &:hover {
      background: #13B1CC;
      border-color: #13B1CC;
    }
  }
  .iconfont {
    margin-right: 4px;
    &.icon-xuanzewenjian {
      font-size: 12px;
    }
    &.icon-xiazai {
      font-size: 13px;
    }
    &.icon-upload {
      font-size: 12px;
    }
    &.icon-leimupinleifenleileibie {
      font-size: 12px;
    }
  }
  .queryBox {
    height: 50px;
    border-bottom: 1px solid #d3d3d3;
    margin: 0 15px 15px;
    position: relative;
    overflow: inherit;
    .queryBtns {
      float: right;
      margin-top: 14px;
    }
  }
  .content_box {
    margin: 0 15px 0;
    .page_box{
      width: 100%;
      padding: 15px;
      text-align: right
    }
  }
  .extract_box{
    height: 100%;
    width: 100%;
    position: absolute;
    right: -120%;
    top:0;
    z-index: 1356;
    background: #fff;
    transition: all 0.8s ease;
    border-radius: 0 0 5px 5px;
    .closeBtn{
      position: absolute;
      right:10px;
      bottom:10px;
      z-index: 131531313;
    }
  }
  .achievementAddActive{
    right:0;
  }
  .btn-add {
    background-color: #01c296;
    border-color: #01c296;
    &:hover, &:focus {
      background-color: #05ab85;
      border-color: #05ab85;
    }
  }
  .btn-exportBtn {
    background: #2d579b;
    border-color: #2d579b;
    &:hover {
      background: #1a3a6f;
      border-color: #1a3a6f;
    }
  }
}
