<template>
  <div id="tablet">
    <div class="queryBox">
      <span class="title"><i class="iconfont icon-shenhe1"></i>人类活动详查详情</span>
      <Button type="primary" size="small" icon="ios-arrow-back" class="sign-btn" @click="backHistory">返回</Button>
    </div>
    <div class="content_box">
      <div>
        <div style="overflow: hidden">
          <div style="float: left;width: 50%;padding: 0 5%;">
            <h3 style="text-align: left;margin-left: 40px;margin-bottom: 30px;font-size: 16px;">下发斑块信息</h3>
            <Form ref="taskEditForm" :model="achievementData1" :label-width="130">
              <Row>
                <Col span="24">
                  <FormItem prop="ck016" label="活动设施类型：">
                    <Input size="default" v-model.trim="achievementData1.activityName" :title="achievementData1.activityName" disabled/>
                  </FormItem>
                </Col>
                <Col span="24">
                  <FormItem prop="cd013" label="活动设施经度：">
                    <Input size="default" v-model.trim="achievementData1.cd013" :title="achievementData1.cd013" disabled/>
                  </FormItem>
                </Col>
                <Col span="24">
                  <FormItem prop="cd014" label="活动设施纬度：">
                    <Input size="default" v-model.trim="achievementData1.cd014" :title="achievementData1.cd014" disabled/>
                  </FormItem>
                </Col>
                <Col span="24">
                  <FormItem prop="center" label="活动设施中点坐标：">
                    <Input size="default" v-model.trim="achievementData1.center" :title="achievementData1.center" disabled/>
                  </FormItem>
                </Col>
                <Col span="24">
                  <FormItem prop="position" label="活动设施中心位置：">
                    <Input size="default" v-model.trim="achievementData1.position" :title="achievementData1.position" disabled/>
                  </FormItem>
                </Col>
                <Col span="24">
                  <FormItem prop="ck012" label="活动设施面积(km²)：">
                    <Input size="default" v-model.trim="achievementData1.area" :title="achievementData1.area" disabled/>
                  </FormItem>
                </Col>
                <Col span="24">
                  <FormItem prop="cd011" label="添加时间：">
                    <Input size="default" v-model.trim="achievementData1.cd011" :title="achievementData1.cd011" disabled/>
                  </FormItem>
                </Col>
              </Row>
            </Form>
          </div>
          <div style="float: right;width: 50%;padding: 0 5%;">
            <h3 style="text-align: left;margin-left: 40px;margin-bottom: 30px;font-size: 16px;">现场核查信息</h3>
            <Form ref="taskEditForm1" :model="achievementData2" :label-width="130">
              <Row>
                <!--                <Col span="24">-->
                <!--                  <FormItem prop="ck010" label="活动设施名称：">-->
                <!--                    <Input size="default" v-model.trim="achievementData2.ck010" :title="achievementData2.ck010" disabled/>-->
                <!--                  </FormItem>-->
                <!--                </Col>-->
                <Col span="24">
                  <FormItem prop="ck016" label="活动设施类型：">
                    <Input size="default" v-model.trim="achievementData2.ck016" :title="achievementData2.ck016" disabled/>
                  </FormItem>
                </Col>
                <Col span="24">
                  <FormItem prop="ck012" label="活动设施面积(km²)：">
                    <Input size="default" v-model.trim="achievementData2.ck011" :title="achievementData2.ck011" disabled/>
                  </FormItem>
                </Col>
                <Col span="24">
                  <FormItem prop="ck076" label="活动设施说明：">
                    <i-input v-model.trim="achievementData2.ck015" type="textarea" :autosize="{minRows: 5}" :title="achievementData2.ck015" disabled></i-input>
                  </FormItem>
                </Col>
                <Col span="24">
                  <FormItem prop="ck086" label="核查时间：">
                    <Input size="default" v-model.trim="achievementData2.ck086" :title="achievementData2.ck086" disabled/>
                  </FormItem>
                </Col>
                <Col span="24">
                  <FormItem prop="ck012" label="建设时间：">
                    <Input size="default" v-model.trim="achievementData2.ck019" :title="achievementData2.ck019" disabled/>
                  </FormItem>
                </Col>
                <Col span="24">
                  <FormItem prop="ck012" label="活动设施现状：">
                    <Input size="default" v-model.trim="achievementData2.ck012" :title="achievementData2.ck012" disabled/>
                  </FormItem>
                </Col>
                <Col span="24">
                  <FormItem prop="ck012" label="有无环评手续：">
                    <Input size="default" v-model.trim="achievementData2.ck021" :title="achievementData2.ck021" disabled/>
                  </FormItem>
                </Col>
                <Col span="24">
                  <FormItem prop="rusername" label="核查人：">
                    <Input size="default" v-model.trim="achievementData2.rusername" :title="achievementData2.rusername" disabled/>
                  </FormItem>
                </Col>
                <Col span="24">
                  <FormItem prop="ck076" label="现场照片：">
                    <ul>
                      <li v-for="(tab,index) in achievementData2.st4ScsCeList" :key="index">
                        <!--图片-->
                        <div v-if="tab.ce004 === 0">
                          <img :src="'/epr/epr/UploadData' + tab.ce003" alt="" style="width: 100%;height: 240px">
                        </div>
                      </li>
                    </ul>
                  </FormItem>
                </Col>
                <Col span="24">
                  <FormItem prop="ck076" label="现场视频：">
                    <ul>
                      <li v-for="(tab,index) in achievementData2.st4ScsCeList" :key="index">
                        <!--视频-->
                        <div v-if="tab.ce004 === 2">
                          <video width="100%" height="240" controls>
                            <source :src="'/epr/epr/UploadData' + tab.ce003">
                          </video>
                        </div>
                      </li>
                    </ul>
                  </FormItem>
                </Col>
              </Row>
            </Form>
          </div>
        </div>
        <p>
          <Button type="primary" @click="auditModal = true" v-if="auditStatus">审核</Button>
          <Button type="primary" @click="auditModal = true" v-else disabled>审核</Button>
        </p>
      </div>
    </div>
    <!--拒绝审核理由弹框 -->
    <Modal
      v-model="auditModal"
      @on-cancel="handleCancel">
      <div>
        <Form :model="auditData" ref="statusForm" :label-width="90" style="padding:0 50px;margin-top: 40px" :rules="DataRule" >
          <FormItem prop="ck070" label="审核意见：">
            <Input type="textarea" v-model.trim="auditData.ck070" placeholder="请输入审核意见" size="default"/>
          </FormItem>
        </Form>
      </div>
      <div slot="footer">
        <Button type="primary" @click="handleAudit" style="margin-right: 10px">审核通过</Button>
        <Button @click="cancelAudit">审核拒绝</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import {
  // 查看详情
  getStageDetails,
  // 审核
  pointStageExamine
} from '@/api/achievement'
export default {
  name: 'achievementDetails',
  data () {
    return {
      // 编辑数据
      achievementData1: {},
      // 编辑数据
      achievementData2: {},
      // 审核意见
      auditModal: false,
      auditData: {
        ck070: ''
      },
      // 审核状态--是否已审核
      auditStatus: true,
      // 审核意见校验
      DataRule: {
        ck070: [
          { required: true, message: '审核意见不能为空', trigger: 'blur' }
        ]
      }
    }
  },
  props: {
    /* 编辑行原数据 */
    data: {}
  },
  watch: {
    data () {
      this.dataHandle()
    }
  },
  methods: {
    /* 编辑框默认值数据处理 */
    dataHandle () {
      if (this.data.ck067 === 0) {
        this.auditStatus = true
      } else if (this.data.ck067 === 1) {
        this.auditStatus = false
      } else if (this.data.ck067 === 2) {
        this.auditStatus = false
      } else {
        this.auditStatus = true
      }
      this.getDetails(this.data.ck001)
    },
    // 显示详情
    getDetails (e) {
      getStageDetails({
        data: {
          ck001: e
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.achievementData1 = res.data.data.point
          this.achievementData2 = res.data.data.ledger
          this.achievementData1.activityName = res.data.data.ledger.st4ScsCd.activityName
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    // 审核通过
    handleAudit () {
      this.$refs.statusForm.validate(valid => {
        if (valid) {
          pointStageExamine({
            data: {
              ck001: this.data.ck001,
              ck067: '1',
              ck070: this.auditData.ck070
            }
          }).then((res) => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '审核通过' })
              this.handleCancel()
              this.getDetails(this.data.ck001)
              this.auditStatus = false
            } else {
              this.$Notice.error({ title: res.data.msg })
              this.handleCancel()
            }
          }).catch(() => {
            this.$Notice.error({ title: '服务器错误' })
            this.handleCancel()
          })
        }
      })
    },
    // 审核拒绝
    cancelAudit () {
      this.$refs.statusForm.validate(valid => {
        if (valid) {
          pointStageExamine({
            data: {
              ck001: this.data.ck001,
              ck067: '2',
              ck070: this.auditData.ck070
            }
          }).then((res) => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '审核拒绝' })
              this.handleCancel()
              this.getDetails(this.data.ck001)
              this.auditStatus = false
            } else {
              this.$Notice.error({ title: res.data.msg })
              this.handleCancel()
            }
          }).catch(() => {
            this.$Notice.error({ title: '服务器错误' })
            this.handleCancel()
          })
        }
      })
    },
    handleCancel () {
      this.auditData = {}
      this.auditModal = false
    },
    // 返回前一页
    backHistory () {
      this.$emit('signOut', false)
      this.$emit('getTableList', false)
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    height: 100%;
    overflow: hidden;
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px;
      position: relative;
      overflow: inherit;
      line-height: 50px;
      text-align: right;
      .title {
        float: left;
        font-size: 14px;
        i {
          vertical-align: middle;
          font-size: 20px;
          margin-right: 5px;
        }
      }
    }
    .content_box {
      height: calc(100% - 50px);
      overflow: hidden;
      &>div {
        padding: 20px 15px;
        height: 100%;
        overflow: auto;
      }
      .ivu-col-offset-6 {
        margin-left: 15%;
      }
    }
  }
</style>

<style>
  .ivu-input {
    color: #000!important;
  }
</style>
