  position: relative;
  height: 100%;
  overflow: hidden;
  .queryBox {
    height: 50px;
    border-bottom: 1px solid #d3d3d3;
    margin: 0 15px;
    position: relative;
    overflow: inherit;
    line-height: 50px;
    text-align: right;
    .title {
      float: left;
      font-size: 14px;
      i {
        vertical-align: middle;
        font-size: 20px;
        margin-right: 5px;
      }
    }
  }
  .content_box {
    height: calc(100% - 50px);
    overflow: hidden;
    &>div {
      padding: 20px 15px;
      height: 100%;
      overflow: auto;
    }
    .ivu-col-offset-6 {
      margin-left: 15%;
    }
  }
}
